import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastController, LoadingController, AlertController } from '@ionic/angular';
import { AccessProviders } from '../../providers/access-providers';
import { HttpClient } from '@angular/common/http';
import { from, Observable, Subscriber } from 'rxjs';

@Component({
  selector: 'app-mod-menuv',
  templateUrl: './mod-menuv.page.html',
  styleUrls: ['./mod-menuv.page.scss'],
})
export class ModMenuvPage implements OnInit {

  id: number;
  numsemana: string="";
  descripcion: string="";
  lunes: string="";
  martes: string="";
  miercoles: string="";
  jueves: string="";
  viernes: string="";
  sabado: string="";
  domingo: string="";

  disabledbutton;

  constructor(
    private router: Router,
    private toastCtrl: ToastController,
    private alertCtrl: AlertController,
    private loadingCtrl: LoadingController,
    private accsPrvds: AccessProviders,
    private actRoute: ActivatedRoute
  ) { }

  ngOnInit() {
    this.actRoute.params.subscribe((data: any)=>{
      console.log(data);
      this.id = data.id;

      if(this.id != 0){
        this.cargarMenu()
      }
    });
  }

  cargarMenu(){
    return new Promise(resolve =>{
      let body = {
        aksi: 'load_single_dataMSV', 
        id: this.id
      }
      this.accsPrvds.postData(body, 'proses_api.php').subscribe((res: any) => {
        this.numsemana = res.result.numsemana;
        this.descripcion = res.result.descripcion;
        this.lunes = res.result.lunes;
        this.martes = res.result.martes;
        this.miercoles = res.result.miercoles;
        this.jueves = res.result.jueves;
        this.viernes = res.result.viernes;
        this.sabado = res.result.sabado;
        this.domingo = res.result.domingo;
      });
    });
  }

  async crudActionguardar(a){
    if(this.numsemana=="",
      this.descripcion=="",
      this.lunes=="",
      this.martes=="",
      this.miercoles=="",
      this.jueves=="",
      this.viernes=="",
      this.sabado=="",
      this.domingo==""
    ){
 
    }else{
      this.disabledbutton = true;
      const loader = await this.loadingCtrl.create({
        message: 'Espere por favor....',
      });
      await loader.present();

      return new Promise(resolve =>{
        let body = {
          aksi: 'mod_crudMSV',
          id: this.id,
          numsemana: this.numsemana,
          descripcion: this.descripcion,
          lunes: this.lunes,
          martes: this.martes,
          miercoles: this.miercoles,
          jueves: this.jueves,
          viernes: this.viernes,
          sabado: this.sabado,
          domingo: this.domingo,
          action: a
        }

        this.accsPrvds.postData(body, 'proses_api.php').subscribe((res:any)=>{
          if(res.success==true){
            loader.dismiss();
            this.disabledbutton = false;
            this.presentToast(res.msg);
            this.router.navigate(['/home']);
          }else{
            loader.dismiss();
            this.disabledbutton = false;
            this.presentAlert(res.msg, a);
          }
        },(err)=>{
          loader.dismiss();
          this.disabledbutton = false;
          this.presentAlert('Timeout',a);
        });
      });
    }
  }

  
  async presentToast(a){
    const toast = await this.toastCtrl.create({
      message: a,
      duration: 1500,
    });
    toast.present();
  }

  async presentAlert(a,b) {
    const alert = await this.alertCtrl.create({
      header: a,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Close',
          handler: (blah) => {
            console.log('Confirm Cancel: blah');
          }
        }, {
          text: 'Try Again',
          handler: () => {
            this.crudActionguardar(b);
          }
        }
      ]
    });

    await alert.present();
  }
  

}
